@import url(https://fonts.googleapis.com/css2?family=Open+Sans);
:root{--background-colour: #333333;--text-colour: #bbbbbb;--box-colour: #111111}html[data-theme='light']{--background-colour: #eee;--box-colour: #ffffff;--text-colour: #000000}html[data-theme='dark']{--background-colour: #333333;--box-colour: #111111;--text-colour: #bbbbbb}body,#root,.App{z-index:-10000000;position:relative;margin:0;font-family:Open Sans,sans-serif;font-size:1.1rem}body{background-color:#333333;background-color:var(--background-colour);background-image:url("data:image/svg+xml,%3Csvg width='6' height='6' viewBox='0 0 6 6' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%23ffffff' fill-opacity='0.05' fill-rule='evenodd'%3E%3Cpath d='M5 0h1L0 6V5zM6 5v1H5z'/%3E%3C/g%3E%3C/svg%3E");color:#bbbbbb;color:var(--text-colour)}body,html{height:100%}.App{overflow:hidden;min-height:100vh;width:80%;margin:0 auto;background-color:#111111;background-color:var(--box-colour);padding:1rem;display:-webkit-flexbox;display:flex;min-height:95vh;flex-direction:column}footer{font-size:.7rem}h1,p{font-family:Open Sans,sans-serif}a:link{color:#bbbbbb;color:var(--text-colour)}a:visited{color:#bbbbbb;color:var(--text-colour)}a:hover{color:lightgray}a:active{color:#bbbbbb;color:var(--text-colour)}.search-bar{width:100%;text-align:center;flex:1 0 auto}.search-bar input[type="search"]{height:3rem;width:70%;font-size:1.5rem;background-color:#bbbbbb;background-color:var(--text-colour);color:#111111;color:var(--box-colour);padding-left:1rem}.search-bar button{height:3rem;width:20%;max-width:4rem;font-size:1.5rem;margin-left:.5rem;margin-right:1rem;background-color:#bbbbbb;background-color:var(--text-colour);color:#111111;color:var(--box-colour);cursor:pointer}hr{border:1px solid #bbbbbb;border:1px solid var(--text-colour)}.results-box{padding:1rem}.results-box .line-box{margin:0 0 2rem 0}.results-box .line-box>div{margin:.3rem 0 0 0}.style-switch{position:fixed;bottom:1rem;right:1rem}.style-switch button{background-color:#bbbbbb;background-color:var(--text-colour);height:1.5rem;width:1.5rem;cursor:pointer;border-color:#111111;border-color:var(--box-colour);border-radius:5px}

